.ai_features-container_feature{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin: 1rem;
}
.ai_features-container_feature-title{
    flex: 1;
    max-width: 400px;
    margin-right: 3rem;
}
.ai_features-container_feature-title h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 25px;
    line-height: 38px;
    letter-spacing: -0.04em;
    color: #222831;
}
.ai_features-container_feature-title div{
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}
.ai_features-container_feature-text{
    flex: 2;
    max-width: 390px;
    display: flex;
}
.ai_features-container_feature-text p{
    font-family: var(--font-family);
    font-weight: 550;
    font-size: 18px;
    line-height: 24px;
    /* color: var(--color-text); */
    color: #393E46;
}
.ai_features-container_feature-title:hover *{
    color: #F49867;
}
@media screen and (max-width: 550px){
    .ai_features-container_feature-title h1{
        font-size: 14px;
        line-height: 20px;
        max-width: 19px;
    }
    .ai_features-container_feature-text p{
        font-size: 12px;
        line-height: 20px;
    }
    .ai_features-container_aifeature{
        margin: 1rem 0;
    }
}